import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { Data } from './models/data';
import { Egypt } from './models/egypt';
import { World } from './models/world';

@Injectable({
  providedIn: 'root'
})
export class DataService {

  constructor(private _HttpClient: HttpClient) { }
  dataTable(): Observable<Data[]> {
    return this._HttpClient.get<Data[]>('https://covid19.mathdro.id/api/recovered');
  }

  egypt(): Observable<Egypt[]> {
    return this._HttpClient.get<Egypt[]>('https://wuhan-coronavirus-api.laeyoung.endpoint.ainize.ai/jhu-edu/latest?iso2=EG&onlyCountries=true');
  }
  world(): Observable<World> {
    return this._HttpClient.get<World>('https://wuhan-coronavirus-api.laeyoung.endpoint.ainize.ai/jhu-edu/brief');
  }

}
